import * as React from "react";
import {find} from "underscore";

import {WidgetActionType, BaseWidget} from "../../../models";
import {HOC} from "../../../hoc";
import {BaseWidgetComponentProps} from "../../../components/widgets/BaseWidgetComponentProps";

export interface WidgetActionHandler {
    widgetAction?: WidgetAction;
}

export interface WidgetAction {
    type: WidgetActionType;
    execute: () => void;
}

export function withAction(
    ...actions: WidgetAction[]
): HOC<BaseWidget & WidgetActionHandler, BaseWidgetComponentProps> {
    return (BaseComponent) => (props) => {
        if (props.action) {
            const widgetAction = find((actions), a => a.type === props.action!.type);

            if (widgetAction) {
                return <BaseComponent {...props} widgetAction={widgetAction} />;
            }
        }

        return <BaseComponent {...props} />;
    };
}



