import {Dispatch} from "redux";
import {ExperienceModel} from "shared/xp/models";
import TransitionDirection from "shared/xp/components/scenes/TransitionDirection";
import {ApplicationState} from "../../store";
import {fetchExperience} from "../../api";
import {
    ActionTypes,
    ExperienceLoadAction,
    ExperienceLoadedAction,
    ExperienceLoadFailedAction,
    ExperienceSceneChangeAction,
    ErroneousApplicationAction
} from "../";

const signalExperienceLoad = (id: string): ExperienceLoadAction => ({
    type: ActionTypes.EXPERIENCE_LOAD,
    payload: {experienceId: id}
});

const signalExperienceLoaded = (model: ExperienceModel): ExperienceLoadedAction => ({
    type: ActionTypes.EXPERIENCE_LOADED,
    payload: {
        experienceId: model.id,
        data: model
    }
});

const signalExperienceLoadFailed = (error: Error, experienceId: string): ExperienceLoadFailedAction => ({
    type: ActionTypes.EXPERIENCE_LOAD_FAILED,
    payload: {
        data: error,
        experienceId
    }
});

const signalError = (error: Error): ErroneousApplicationAction => ({
    type: ActionTypes.APPLICATION_ERROR,
    payload: error
});

export const loadExperience = (id: string) => async (
    dispatch: Dispatch<ApplicationState>,
    getState: () => ApplicationState
) => {
    dispatch(signalExperienceLoad(id));

    // Check if cached...
    const {viewer} = getState();
    const cachedExperienceState = viewer.experiences[id];

    if (cachedExperienceState && cachedExperienceState.model) {
        dispatch(signalExperienceLoaded(cachedExperienceState.model));
    } else {
        try {
            const model = await fetchExperience(id);
            dispatch(signalExperienceLoaded(model));
        } catch (error) {
            dispatch(signalExperienceLoadFailed(error, id));
        }
    }
};

const changeScene = (
    experienceId: string,
    sceneNumber: number,
    transitionDirection: TransitionDirection
): ExperienceSceneChangeAction => ({
    type: ActionTypes.EXPERIENCE_SCENE_CHANGED,
    payload: {
        experienceId,
        sceneNumber,
        transitionDirection
    }
});

export const goToScene = (scene: number, experienceId: string, transitionDirection?: TransitionDirection) => (
    dispatch: Dispatch<ApplicationState>,
    getState: () => ApplicationState
) => {
    if (transitionDirection === undefined) {
        const {viewer} = getState();
        const experienceState = viewer.experiences[experienceId];

        transitionDirection =
            experienceState && experienceState.currentSceneNumber > scene
                ? TransitionDirection.BACKWARDS
                : TransitionDirection.FORWARDS;
    }

    dispatch(changeScene(experienceId, scene, transitionDirection));
};

export const nextScene = (experienceId: string) => (
    dispatch: Dispatch<ApplicationState>,
    getState: () => ApplicationState
) => {
    const {viewer} = getState();
    const experienceState = viewer.experiences[experienceId];

    if (!experienceState) {
        return dispatch(signalError(new Error(`No experience with id ${experienceId}`)));
    }

    const {currentSceneNumber} = experienceState;
    let next = currentSceneNumber + 1;

    if (next > experienceState.model!.scenes.length) {
        next = 1;
    }

    dispatch(changeScene(experienceId, next, TransitionDirection.FORWARDS));
};

export const previousScene = (experienceId: string) => (
    dispatch: Dispatch<ApplicationState>,
    getState: () => ApplicationState
) => {
    const {viewer} = getState();
    const experienceState = viewer.experiences[experienceId];

    if (!experienceState) {
        return dispatch(signalError(new Error(`No experience with id ${experienceId}`)));
    }

    const {currentSceneNumber} = experienceState;
    let previous = currentSceneNumber - 1;

    if (previous < 1) {
        previous = experienceState.model!.scenes.length;
    }

    dispatch(changeScene(experienceId, previous, TransitionDirection.BACKWARDS));
};



