import * as React from "react";
import {Component} from "react";

import ViewportListener from "../../util/ViewportListener";

import {Rect, Size, Point} from "shared/xp/geom";
import {HOC} from "shared/xp/hoc";

interface BaseComponentProps {
    frame: Rect;
}

interface HOCProps {
    originalSize: Size;
}

interface HOCState {
    frame: Rect;
}

type OutputProps = BaseComponentProps & HOCState;

const withWindowLayout: HOC<OutputProps, HOCProps> = BaseComponent =>
    class extends Component<HOCProps, HOCState> {

        state = {
            frame: Rect.zero
        };

        private viewportListener: ViewportListener;

        componentWillMount() {
            this.viewportListener = new ViewportListener(this.updateStateFromCurrentViewportSize)
                .listen()
                .trigger();
        }

        componentWillUnmount() {
            this.viewportListener.stopListening();
        }

        render() {
            const {frame} = this.state;

            return <BaseComponent frame={frame} {...this.props} />;
        }

        private updateStateFromCurrentViewportSize = ({width, height}: Size) => {
            const {originalSize} = this.props;
            const maxSize = new Size(
                Math.min(width, originalSize.width),
                Math.min(height, originalSize.height)
            );
            const frameSize = maxSize.shrinkToAspectRatio(originalSize.aspectRatio);
            const framePosition = new Point(
                width / 2 - frameSize.width / 2,
                height / 2 - frameSize.height / 2
            );
            const frame = new Rect(framePosition, frameSize);

            this.setState({frame});
        }
    };

export default withWindowLayout;



