import {FontFamily} from "../models";

/**
 * @interface
 * Every font loader provider should support this interface
 */
export interface FontLoaderProvider {
    /**
     * Check if current provider can load requested font
     * @param {FontFamily} font
     * @return {boolean}
     */
    support(font: FontFamily): boolean;

    /**
     * Try to load requested font
     * @param {FontFamily} font
     * @return {Promise<FontFamily | Error>}
     */
    load(font: FontFamily): Promise<FontFamily | Error>;
}

/**
 * @class
 * Load fonts from any supported destination into the app
 */
export class FontLoader {
    private _providers: FontLoaderProvider[] = [];

    /**
     * Current registered providers
     * @return {FontLoaderProvider[]}
     */
    get providers(): FontLoaderProvider[] {
        return this._providers;
    }

    /**
     * Try to register new provider. if provider with current type exist then it should be skipped
     * @param {FontLoaderProvider} provider
     * @return {boolean}
     */
    registerProvider(provider: FontLoaderProvider): boolean {
        if (this._providers.find((item) => provider.constructor === item.constructor)) {
            return false;
        }
        this._providers.push(provider);
        return true;
    }

    /**
     * Try to load FontFamily using registered providers
     * @param {FontFamily} font
     * @return {Promise<FontFamily | Error>}
     */
    load(font: FontFamily): Promise<FontFamily | Error> {
        let provider = this._providers.reverse().find((p: FontLoaderProvider) => p.support(font));
        if (!provider) {
            return Promise.reject(new Error(`Unable to find FontLoaderProvider for font "${font.fontFamily}"`));
        }
        return provider.load(font);
    }

    /**
     * Try to load fonts from the array of FontFamily's
     * @param {FontFamily[]} fonts
     * @return {Promise<(FontFamily | Error)[]>}
     */
    loadFromArray(fonts: FontFamily[]) {
        return Promise.all(fonts.map((font) => this.load(font)));
    }
}

/**
 * @FIXME: what is the proper way to instantiate "singletons"
 * or actually app\module scope wide objects (similar Injector\IoC\DI)
 * @type {FontLoader}
 */
const fontLoader = new FontLoader();
export default fontLoader;



