import * as React from "react";
import * as ReactDOM from "react-dom";
import {CSSProperties, PureComponent, ReactElement, cloneElement, StatelessComponent, ReactNode, Children} from "react";
import {TweenLite} from "gsap";
import {SceneModel, SceneTransition} from "shared/xp/models";
import {Size, Point} from "../../geom";
import {SceneProps} from "./Scene";
import TransitionDirection from "./TransitionDirection";

const DEFAULT_TRANSITION_DURATION = 0.5; // in secs

const calculateCircularIndex = (index: number, length: number) => {
    if (index < 0) {
        return length + index % length;
    }

    return index % length;
};

const calculateCircularDistance = (from: number, to: number, length: number, forward: boolean = true) => {
    if (forward) {
        return to > from ? to - from : length - from + to;
    }

    return to < from ? from - to : length + from - to;
};

const determineActiveState = (
    componentIndex: number,
    currentIndex: number,
    previousIndex: number,
    transitioning: boolean
) => {
    if (transitioning) {
        // If the scene is currently leaving the viewport, keep it active until the transition completes.
        return componentIndex === previousIndex;
    }

    // Done transitioning - set current scene to be "active".
    return componentIndex === currentIndex;
};

enum SlideTransitionAxis {
    HORIZONTAL,
    VERTICAL
}

interface ScenesContainerProps {
    axis: SlideTransitionAxis;
    sceneSize: Size;
    children: ReactNode;
    reverse: boolean;
}

const TrackLayout: StatelessComponent<ScenesContainerProps> = ({axis, sceneSize, children, reverse}) => {
    const numChildren = Children.count(children);
    const horizontal = axis === SlideTransitionAxis.HORIZONTAL;
    const x = horizontal ? -sceneSize.width : 0;
    const y = horizontal ? 0 : -sceneSize.height;
    const width = horizontal ? numChildren * sceneSize.width : sceneSize.width;
    const height = horizontal ? sceneSize.height : numChildren * sceneSize.height;

    const style: CSSProperties = {
        height,
        width,
        position: "absolute",
        transform: `translate3d(${x}px, ${y}px, 0)`
    };

    const sceneStyle = {
        float: horizontal ? "left" : "none"
    };

    let trackLayoutChildren = Children.toArray(children);

    if (reverse) {
        trackLayoutChildren = trackLayoutChildren.reverse();
    }

    return (
        <div className="scenes-container" style={style}>
            {trackLayoutChildren.map(child =>
                cloneElement(child as ReactElement<SceneProps>, {style: sceneStyle} as SceneProps)
            )}
        </div>
    );
};

interface TrackProps {
    sceneSize: Size;
    scenes: ReadonlyArray<SceneModel>;
    axis: SlideTransitionAxis;
    index: number;
    previousIndex: number;
    reverse?: boolean;
    transitioning?: boolean;
}

const Track: StatelessComponent<TrackProps> = ({
    scenes,
    sceneSize,
    children,
    axis,
    index,
    previousIndex,
    reverse = false,
    transitioning = false
}) => {
    const currentIndex = calculateCircularIndex(index, scenes.length);
    const positionIndex = reverse ? -index : index;
    const x = axis === SlideTransitionAxis.HORIZONTAL ? sceneSize.width * positionIndex : 0;
    const y = axis === SlideTransitionAxis.VERTICAL ? sceneSize.height * positionIndex : 0;

    const style = {
        transform: `translate3d(${x}px, ${y}px, 0)`
    };

    const trackChildren = Children.toArray(children);

    // We need at least 3 scene components (in order to provide a smooth UX).
    // "copy" scene 1 and position it at the end...
    if (trackChildren.length === 2) {
        const first = trackChildren[0] as ReactElement<SceneProps>;
        const cloneProps: Partial<SceneProps> = {
            key: `${first.props.model.id}-clone`
        };

        trackChildren.push(cloneElement(first, cloneProps));
    }

    const trackElements = trackChildren.map(child => {
        const element = child as ReactElement<SceneProps>;
        const i = scenes.indexOf(element.props.model);

        const props: Partial<SceneProps> = {
            hidden: i !== currentIndex && !transitioning,
            active: determineActiveState(
                i,
                currentIndex,
                calculateCircularIndex(previousIndex, scenes.length),
                transitioning
            )
        };

        return cloneElement(element, props);
    });

    return (
        <div className="track">
            <div className="track-container" style={style}>
                <TrackLayout sceneSize={sceneSize} axis={axis} reverse={reverse}>
                    {trackElements}
                </TrackLayout>
            </div>
        </div>
    );
};

interface SceneSlideTransitionProps {
    sceneSize: Size;
    scene: SceneModel;
    scenes: ReadonlyArray<SceneModel>;
    transition: SceneTransition;
    transitionDirection: TransitionDirection;
}

interface TrackTransitionState {
    index: number;
    previousIndex: number;
    transitioning: boolean;
    pendingTransition: boolean;
    pendingIndexNormalization: boolean;
    pendingScenePositioning: boolean;
}

/**
 * Handles all "slide" transitions (e.g. `SceneTransition.SLIDE_FROM_BOTTOM`, `SceneTransition.SLIDE_FROM_LEFT` etc.)
 * and`SceneTransition.NONE`, the latter will just set the transition duration to `0`.
 */
class SceneSlideTransition extends PureComponent<SceneSlideTransitionProps, TrackTransitionState> {
    state = {
        index: 0,
        previousIndex: 0,
        transitioning: false,
        pendingTransition: false,
        pendingIndexNormalization: false,
        pendingScenePositioning: false
    };

    private node: Element;

    private duration: number;

    private axis: SlideTransitionAxis;

    private reverse: boolean;

    constructor(props: SceneSlideTransitionProps) {
        super(props);

        const {transition} = props;

        this.duration = transition === SceneTransition.NONE ? 0 : DEFAULT_TRANSITION_DURATION;
        this.reverse = transition === SceneTransition.SLIDE_FROM_TOP || transition === SceneTransition.SLIDE_FROM_LEFT;
        this.axis =
            transition === SceneTransition.SLIDE_FROM_TOP || transition === SceneTransition.SLIDE_FROM_BOTTOM
                ? SlideTransitionAxis.VERTICAL
                : SlideTransitionAxis.HORIZONTAL;
    }

    componentWillReceiveProps({scene, transitionDirection, sceneSize}: SceneSlideTransitionProps) {
        if (scene !== this.props.scene) {
            this.updateIndexFromScene(scene, transitionDirection);
        }
        // Scene size changed, make sure to schedule a relayout
        if (!this.props.sceneSize.equals(sceneSize)) {
            this.setState({pendingScenePositioning: true});
        }
    }

    componentWillMount() {
        const {scenes, scene} = this.props;
        this.setState({index: scenes.indexOf(scene)});
    }

    componentDidMount() {
        this.node = ReactDOM.findDOMNode(this);
        this.updateCurrentPosition();
    }

    render() {
        const {children, sceneSize, scenes} = this.props;
        const {index, transitioning, previousIndex} = this.state;

        return (
            <Track
                scenes={scenes}
                index={index}
                previousIndex={previousIndex}
                transitioning={transitioning}
                sceneSize={sceneSize}
                axis={this.axis}
                reverse={this.reverse}
            >
                {children}
            </Track>
        );
    }

    componentDidUpdate() {
        const {pendingTransition, pendingIndexNormalization, pendingScenePositioning} = this.state;

        if (pendingTransition) {
            this.performTransition();
        }

        if (pendingIndexNormalization || pendingScenePositioning) {
            this.updateCurrentPosition();

            this.setState({pendingIndexNormalization: false, pendingScenePositioning: false});
        }
    }

    private updateCurrentPosition() {
        const {x, y} = this.calculateCurrentPosition();
        TweenLite.set(this.node, {x, y});
    }

    private updateIndexFromScene(scene: SceneModel, transitionDirection?: TransitionDirection) {
        this.setState((prevState, props) => {
            let {index} = prevState;
            const {scenes} = props;
            const realIndex = calculateCircularIndex(index, scenes.length);
            const newIndex = scenes.indexOf(scene);
            const direction = transitionDirection || props.transitionDirection;
            const forward = direction === TransitionDirection.FORWARDS;
            const pendingTransition = index !== newIndex;
            const distance = calculateCircularDistance(realIndex, newIndex, scenes.length, forward);

            index = forward ? index + distance : index - distance;

            return {index, previousIndex: prevState.index, pendingTransition, transitioning: pendingTransition};
        });
    }

    private transitionCompleteHandler = () => {
        this.setState(({index}, {scenes}) => {
            const pendingIndexNormalization = index < 0 || index >= scenes.length;
            index = calculateCircularIndex(index, scenes.length);

            return {transitioning: false, index, pendingIndexNormalization};
        });
    }

    private calculateCurrentPosition() {
        const {index} = this.state;
        const {width, height} = this.props.sceneSize;
        const positionIndex = this.reverse ? index : -index;

        const x = this.axis === SlideTransitionAxis.HORIZONTAL ? positionIndex * width : 0;
        const y = this.axis === SlideTransitionAxis.VERTICAL ? positionIndex * height : 0;

        return new Point(x, y);
    }

    private performTransition() {
        const {x, y} = this.calculateCurrentPosition();

        TweenLite.to(this.node, this.duration, {
            x,
            y,
            onComplete: this.transitionCompleteHandler
        });

        this.setState({pendingTransition: false});
    }
}

export default SceneSlideTransition;



