import {compose} from "recompose";

import Image, {ImageProps} from "./Image";
import {withBaseDomainUrl, withCreatorImageSize} from "./hoc";
import {ImageWidgetModel} from "../../../models";
import {WidgetSize} from "./types";
import {DEFAULT_ACTIONS} from "../actions";
import {withDefaultWidgetBehavior, withAction} from "../hoc";

export default compose<ImageProps, ImageWidgetModel & WidgetSize>(
    withAction(...DEFAULT_ACTIONS),
    withDefaultWidgetBehavior,
    withCreatorImageSize,
    withBaseDomainUrl
)(Image);



