import {SyntheticEvent} from "react";
import {withHandlers, withState, compose} from "recompose";

export interface LoadErrorHandler {
    onErrorHandler: React.ReactEventHandler<HTMLImageElement>;
}

export interface FailedLoadImageState {
    failedInitialLoad: boolean;
    setFailedInitialLoad: (failedInitialLoad: boolean) => void;
}

export const withImageErrorHandler = compose(
    withState<FailedLoadImageState, boolean, "failedInitialLoad", "setFailedInitialLoad">(
        "failedInitialLoad",
        "setFailedInitialLoad",
        false
    ),
    withHandlers<FailedLoadImageState, {}>({
        onErrorHandler: ({setFailedInitialLoad}) => (_event: SyntheticEvent<HTMLImageElement>) => {
            setFailedInitialLoad(true);
        }
    })
);



