import fontLoader from "../shared/xp/utils/FontLoader";
import {
    CustomFontLoaderProvider, FontsDotComLoaderProvider,
    GoogleFontLoaderProvider, StandardFontLoaderProvider,
    TypekitFontLoaderProvider
} from "../shared/xp/utils/FontLoaderProviders";

export function initializeEnvironment() {

    // FONT LOADER
    fontLoader.registerProvider(new TypekitFontLoaderProvider());
    fontLoader.registerProvider(new CustomFontLoaderProvider());
    fontLoader.registerProvider(new FontsDotComLoaderProvider());
    fontLoader.registerProvider(new GoogleFontLoaderProvider());
    fontLoader.registerProvider(new StandardFontLoaderProvider());
}



