import * as React from "react";
import {Component} from "react";
import {Dispatch} from "redux";
import {connect} from "react-redux";
import {compose} from "recompose";
import {ApplicationState} from "../../store";
import {loadGroup} from "../../actions";

import {HOC} from "shared/xp/hoc";
import {BreakpointGroupModel} from "shared/xp/models";

interface BaseComponentProps {
    model: BreakpointGroupModel;
}

interface HOCProps {
    dispatch: Dispatch<ApplicationState>;
    groupId: string;
    model: BreakpointGroupModel;
}

const withBreakpointGroupModel: HOC<BaseComponentProps, HOCProps> = (BaseComponent) => (

    class extends Component<HOCProps> {

        static defaultProps = {
            model: null
        };

        componentDidMount() {
            this.signalGroupLoad();
        }

        signalGroupLoad() {
            const {dispatch, groupId} = this.props;

            dispatch(loadGroup(groupId));
        }

        render() {
            const {model} = this.props;

            if (model) {
                return <BaseComponent model={model} />;
            }

            return <span role="img" aria-label="Loading"></span>;
        }

    }
);

const mapStateToProps = ({viewer}: ApplicationState) => ({model: viewer.content && viewer.content.group!.model});

export default compose(
    connect(mapStateToProps),
    withBreakpointGroupModel
);



