import {loadScript} from "../../../net";

const BASE_URL = "https://www.youtube.com/iframe_api";

let youTubeIframeAPI: typeof YT;

/**
 * Resolves a reference to the YouTube API.
 */
export default async (): Promise<typeof YT> => {
    if (youTubeIframeAPI) {
        return youTubeIframeAPI;
    }

    return new Promise<typeof YT>((resolve) => {
        // Create callback in the global scope
        window.onYouTubeIframeAPIReady = () => {
            // Cleanup and resolve
            delete window.onYouTubeIframeAPIReady;
            youTubeIframeAPI = window.YT;

            resolve(youTubeIframeAPI);
        };
        // Start loading the script
        loadScript(BASE_URL);
    });
};



