import {compose, withProps} from "recompose";

import Image, {ImageProps} from "./Image";
import {withBaseDomainUrl} from "./hoc";
import {withDefaultWidgetBehavior, pickProps, withAction} from "../hoc";
import {ImageWidgetModel, SceneNavigationAction} from "../../../models";
import {DEFAULT_ACTIONS} from "../actions";

const DEFAULT_NEXT_URL = "images/image_default_next_scene.svg";
const DEFAULT_PREV_URL = "images/image_default_previous_scene.svg";

const withImagePropsFromModel = (model: ImageWidgetModel) => {
    const sceneAction = model.action as SceneNavigationAction;
    let src = model.src;

    if (!src) {
        src = sceneAction.target === "next" ? DEFAULT_NEXT_URL : DEFAULT_PREV_URL;
    }

    return {
        src,
        altText: sceneAction.target === "next" ? "next scene" : "previous scene"
    };
};

export default compose<ImageProps, ImageWidgetModel>(
    withAction(...DEFAULT_ACTIONS),
    withDefaultWidgetBehavior,
    withProps(withImagePropsFromModel),
    withBaseDomainUrl,
    pickProps<ImageProps, ImageProps>("url", "altText")
)(Image);



