import * as React from "react";
import {StatelessComponent, CSSProperties} from "react";
import {compose} from "recompose";
import {
    ButtonWidgetModel, 
    IconModel, 
    FontStyleType, 
    FontWeightType, 
    ButtonWidgetTypes, 
    IconPlacementTypes
} from "../../../models";
import {
    withDefaultWidgetBehavior,
    withAction,
    withWidgetStyling
} from "../hoc";
import {DEFAULT_ACTIONS} from "../actions";
import {ButtonIconComponent} from "./ButtonIconComponent";
import {ButtonTextComponent} from "./ButtonTextComponent";

const DEFAULT_STYLE: CSSProperties = {
    userSelect: "none",
    pointerEvents: "none",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "transparent",
    width: "auto",
    height: "auto",
    margin: "0"
};

export interface ButtonWidgetComponentProps {
    label: string;
    icon: IconModel;
    backgroundColor: string;
    borderColor?: string;
    borderWidth?: string;
    buttonType: ButtonWidgetTypes;
    cid: string;
    color: string;
    fontFamily: string;
    fontSize: number;
    fontStyle: FontStyleType;
    fontWeight: FontWeightType;
    layerIndex: number;
    opacity: number;
    padding: number;
    radius: number;
}

export const ButtonWidgetComponent: StatelessComponent<ButtonWidgetComponentProps> = (props) => {

    const {
        label, 
        icon, 
        backgroundColor, 
        buttonType, 
        color, 
        fontFamily, 
        fontSize, 
        fontStyle, 
        fontWeight, 
        opacity,
        padding, 
        radius 
    } = props;

    // four possible versions of the widget: text, icon, text+icon, icon+text 
    const isText: boolean = buttonType === ButtonWidgetTypes.TEXT_ONLY ;
    const isIcon: boolean = buttonType === ButtonWidgetTypes.ICON_ONLY ;
    const isIconText: boolean = 
        buttonType === ButtonWidgetTypes.TEXT_AND_ICON && icon.placement === IconPlacementTypes.BEFORE;
    const isTextIcon: boolean = 
        buttonType === ButtonWidgetTypes.TEXT_AND_ICON && icon.placement === IconPlacementTypes.AFTER;

    let paddingStyle: CSSProperties = {
        padding: `6px ${padding}px`
    };

    if(isIcon) {
        paddingStyle = { padding };
    }

    const buttonStyle: CSSProperties = {
        ...DEFAULT_STYLE,
        ...paddingStyle, 
        color,
        backgroundColor,
        fontFamily,
        fontStyle,
        fontWeight,
        fontSize,
        opacity,
        border: "0",
        borderRadius: radius,
        minWidth: fontSize,
        minHeight: fontSize,
        background: backgroundColor
    };

    return (
        <button type="button" style={buttonStyle}>

            {(isIcon || isIconText) && <ButtonIconComponent icon={icon} fontSize={fontSize} buttonType={buttonType} />}

            {(isText || isIconText || isTextIcon) && <ButtonTextComponent label={label} />}

            {isTextIcon && <ButtonIconComponent icon={icon} fontSize={fontSize} buttonType={buttonType} />}
        
        </button>
    );

};

export default compose<ButtonWidgetComponentProps, ButtonWidgetModel >(
    withAction(...DEFAULT_ACTIONS),
    withDefaultWidgetBehavior,
    withWidgetStyling
)(ButtonWidgetComponent);


