import * as React from "react";
import {Attributes, CSSProperties, StatelessComponent} from "react";
import {SceneModel, Integrations} from "../../models";
import {Size} from "../../geom";
import {WidgetsListComponent} from "../widgets/WidgetsListComponent";

/**
 * Extend `Attributes` to visualize `key` as a valid prop.
 */
export interface SceneProps extends Attributes {
    model: SceneModel;
    size: Size;
    integrations?: ReadonlyArray<Integrations>;
    active?: boolean;
    hidden?: boolean;
    style?: CSSProperties;
}

const Scene: StatelessComponent<SceneProps> = ({model, size, integrations, style, active = false, hidden = false}) => {
    const {sceneNumber, widgets} = model;
    const {width, height} = size;

    const sceneStyle: CSSProperties = {
        position: "relative",
        width,
        height,
        visibility: hidden ? "hidden" : "visible",
        ...style
    };

    return (
        <div className={`scene-${sceneNumber}`} style={sceneStyle}>
            <WidgetsListComponent
                widgets={widgets}
                active={active}
                integrations={integrations}
                size={size}
            />
        </div>
    );
};

export default Scene;



