import {ExperienceState} from "../store";
import {ExperienceAction, ActionTypes} from "../actions";
import TransitionDirection from "shared/xp/components/scenes/TransitionDirection";

const DEFAULT_STATE: ExperienceState = {
    model: undefined,
    currentSceneNumber: 1,
    transitionDirection: TransitionDirection.FORWARDS
};

export default (state: ExperienceState = DEFAULT_STATE, action: ExperienceAction): ExperienceState => {
    switch (action.type) {
        case ActionTypes.EXPERIENCE_LOADED:
            return {...state, model: action.payload.data};
        case ActionTypes.EXPERIENCE_SCENE_CHANGED:
            return {
                ...state,
                currentSceneNumber: action.payload.sceneNumber,
                transitionDirection: action.payload.transitionDirection
            };
        default:
            return state;
    }
};



