import * as React from "react";
import {Component, cloneElement} from "react";
import {uniqueId} from "underscore";
import {BaseWidget, Widget, WidgetTypes, Integrations} from "../../models";
import {Size} from "../../geom";
import {withPixelSize} from "./hoc";
import {
    imageWidgetFactory,
    EmbedWidgetComponent,
    ShapeWidgetComponent,
    UnknownWidgetComponent,
    LineWidgetComponent,
    SocialSharingWidgetComponent,
    ButtonWidgetComponent,
    YouTubeWidgetComponent,
    LinkWidgetComponent,
    TextWidgetComponent,
    CompositeWidgetComponent
} from ".";

export interface WidgetComponents {
    widgets: ReadonlyArray<Widget>;
    size: Size;
    integrations?: ReadonlyArray<Integrations>;
}

const createWidgetComponent = (
    widget: Widget,
    size: Size,
    integrations?: ReadonlyArray<Integrations>
) => {
    const widgetWithPxSize = withPixelSize(widget, size);

    switch (widgetWithPxSize.type) {
        case WidgetTypes.LINE:
            return <LineWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.SHAPE:
            return <ShapeWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.IMAGE:
            return imageWidgetFactory(widgetWithPxSize, size, integrations);
        case WidgetTypes.SOCIAL_SHARING:
            return <SocialSharingWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.YOUTUBE:
            return <YouTubeWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.EMBED:
            return <EmbedWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.BUTTON:
            return <ButtonWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.LINK:
            return <LinkWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.TEXT:
            return <TextWidgetComponent {...widgetWithPxSize} />;
        case WidgetTypes.COMPOSITE:
            const withSizeAndIntegrations = {
                ...widgetWithPxSize,
                size,
                integrations
            };
            return <CompositeWidgetComponent {...withSizeAndIntegrations} />;
        default:
            return <UnknownWidgetComponent {...widget as BaseWidget} />;
    }
};

const keyMap: Map<Widget, string> = new Map();
const keyFactory = (widget: Widget) => {
    if (keyMap.has(widget)) {
        return keyMap.get(widget);
    }
    const id = uniqueId("widget-");
    keyMap.set(widget, id);
    return id;
};

interface WidgetsListComponentProps {
    widgets: ReadonlyArray<Widget>;
    size: Size;
    active: boolean;
    integrations?: ReadonlyArray<Integrations>;
}

// TODO: Should be a StatelessComponent
// https://github.com/DefinitelyTyped/DefinitelyTyped/pull/19363
export class WidgetsListComponent extends Component<WidgetsListComponentProps> {
    render() {
        const {widgets, size, active, integrations} = this.props;

        return widgets.map(widget => {
            const element = createWidgetComponent(widget, size, integrations);
            return cloneElement(element, {key: keyFactory(widget), active});
        });
    }
}



