import * as React from "react";
import {StatelessComponent} from "react";
import {compose} from "recompose";

import {withDefaultWidgetBehavior} from "../widgets/hoc/";
import {BaseWidget} from "../../models";

const UnknownWidget: StatelessComponent = () => <p>Widget</p>;

const UnknownWidgetComponent = compose<{}, BaseWidget>(
    withDefaultWidgetBehavior
)(UnknownWidget);

export default UnknownWidgetComponent;



