import * as React from "react";
import {StatelessComponent, CSSProperties} from "react";
import {compose} from "recompose";
import {DEFAULT_ACTIONS} from "./actions";
import {
    withDefaultWidgetBehavior,
    withAction
} from "./hoc/";
import {LinkWidgetModel} from "../../models";

const DEFAULT_STYLE: CSSProperties = {
    opacity: 0,
    cursor: "pointer",
    position: "relative",
    width: "100%",
    height: "100%"
};

const LinkWidget: StatelessComponent = () => {

    return <div style={DEFAULT_STYLE} />;
};

export const LinkWidgetComponent = compose<{}, LinkWidgetModel>(
    withAction(...DEFAULT_ACTIONS),
    withDefaultWidgetBehavior
)(LinkWidget);


