import * as React from "react";
import {StatelessComponent} from "react";
import {compose} from "recompose";
import {withDefaultWidgetBehavior, withWidgetStyling, pickProps} from "./hoc";
import {ShapeWidgetModel} from "../../models";

const DEFAULT_STYLE: React.CSSProperties = {
    position: "relative",
    width: "100%",
    height: "100%"
};

interface WidgetComponent {
    backgroundColor: string;
}

export const ShapeWidgetComponent: StatelessComponent<WidgetComponent> = ({backgroundColor}) => {
    const style = {
        ...DEFAULT_STYLE,
        backgroundColor
    };

    return <div style={style} />;
};

export default compose<WidgetComponent, ShapeWidgetModel>(
    withDefaultWidgetBehavior,
    withWidgetStyling,
    pickProps<ShapeWidgetModel, WidgetComponent>("backgroundColor")
)(ShapeWidgetComponent);



