import * as React from "react";
import {StatelessComponent, CSSProperties} from "react";
import {IconModel, ButtonWidgetTypes, IconPlacementTypes} from "../../../models";
import {ExternalSvgIconComponent} from "./ExternalSvgIconComponent";

export interface ButtonIconComponentProps {
    icon: IconModel;
    fontSize: number;
    buttonType: ButtonWidgetTypes;
}

export const ButtonIconComponent: StatelessComponent<ButtonIconComponentProps> = (props) => {

    const {icon, fontSize, buttonType} = props;

    // build path to SVG
    const iconSvgPath: string = 
    `images/material-design-icons/${icon.groupId}/svg/production/ic_${icon.iconId}_${icon.size}px.svg`;

    // setup SVG styles
    let iconSvgMarginStyle: CSSProperties = {
        marginLeft: 0,
        marginRight: 0
    };
    if(buttonType === ButtonWidgetTypes.TEXT_AND_ICON) {
        if(icon.placement === IconPlacementTypes.AFTER) {
            iconSvgMarginStyle = {
                marginLeft: 5,
                marginRight: 0,
            };
        } else {
            iconSvgMarginStyle = {
                marginLeft: 0,
                marginRight: 5
            };
        }
    }

    const iconStyle: CSSProperties = {
        ...iconSvgMarginStyle,
        display: "inline-block",
        verticalAlign: "middle",
        width: fontSize,
        height: fontSize,
        lineHeight: 0
    };

    const iconSvgStyle: CSSProperties = {
        fill: icon.color,
        width: fontSize,
        height: fontSize
    };

    return (
        <span style={iconStyle}>
            <ExternalSvgIconComponent iconSvgPath={iconSvgPath} iconStyle={iconSvgStyle} iconSize={fontSize} />
        </span>
    );
};

export default ButtonIconComponent;


