import * as React from "react";
import {StatelessComponent} from "react";
import {compose} from "recompose";
import {withDefaultWidgetBehavior} from "./hoc";
import {Widget, CompositeWidgetModel} from "../../models";
import {Size} from "../../geom";
import {WidgetsListComponent} from "shared/xp/components/widgets/WidgetsListComponent";
import {Integrations} from "../../models/integrations";

interface CompositeWidgetComponentProps {
    group: ReadonlyArray<Widget>;
    size: Size;
    active: boolean;
    integrations: ReadonlyArray<Integrations>;
}

export const CompositeWidgetComponent: StatelessComponent<CompositeWidgetComponentProps> = (props) => {
    const {group, integrations, size, active} = props;

    return (
        <div className="group">
            <WidgetsListComponent widgets={group} active={active} integrations={integrations} size={size} />
        </div>
    );
};

export default compose<CompositeWidgetComponentProps, CompositeWidgetModel>(
    withDefaultWidgetBehavior
)(CompositeWidgetComponent);



