import {compose, nest} from "recompose";

import {ExperienceViewer, ExperienceViewerProps} from "../components/ExperienceViewer";
import {withExperienceModel, withExperienceWindowLayout} from "../hoc";
import BreakpointObserver from "./BreakpointObserver";

import {BreakpointGroupModel} from "shared/xp/models";

const EnhancedViewer = compose<ExperienceViewerProps, BreakpointGroupModel>(
    withExperienceModel,
    withExperienceWindowLayout
)(ExperienceViewer);

export default nest(BreakpointObserver, EnhancedViewer);



