import * as React from "react";
import {Component, CSSProperties} from "react";

import * as css from "./ExperienceViewer.css";

import {noop} from "underscore";
import {connect, Dispatch} from "react-redux";
import {ExperienceModel} from "shared/xp/models";
import Rect from "shared/xp/geom/Rect";
import Scenes from "shared/xp/components/scenes/Scenes";
import TransitionDirection from "shared/xp/components/scenes/TransitionDirection";
import {ApplicationState} from "../store";
import {previousScene, nextScene} from "../actions";

export interface ExperienceViewerProps {
    model: ExperienceModel;
    sceneNumber?: number;
    transitionDirection?: TransitionDirection;
    frame?: Rect;
    previousClickHandler?: () => void;
    nextClickHandler?: () => void;
}

export class ExperienceViewer extends Component<ExperienceViewerProps> {
    static defaultProps: Partial<ExperienceViewerProps> = {
        sceneNumber: 1,
        transitionDirection: TransitionDirection.FORWARDS,
        frame: new Rect(0, 0, 800, 600),
        previousClickHandler: noop,
        nextClickHandler: noop
    };

    render() {
        const {frame, model, sceneNumber, transitionDirection, previousClickHandler, nextClickHandler} = this.props;

        const style: CSSProperties = {
            width: frame!.width,
            height: frame!.height,
            transform: `translate3d(${frame!.x}px, ${frame!.y}px, 0px)`,
            backgroundColor: model.backdrop.color
        };

        return (
            <div style={style} className={css.experienceViewer}>
                <div>
                    <Scenes
                        size={frame!.size}
                        overlayScene={model.overlayScene}
                        backgroundScene={model.backgroundScene}
                        scenes={model.scenes}
                        current={sceneNumber!}
                        integrations={model.integrations}
                        transition={model.transition}
                        transitionDirection={transitionDirection!}
                    />
                </div>
                <div style={{position: "absolute", top: 0}}>
                    <button onClick={previousClickHandler}>&lt;</button>
                    <button onClick={nextClickHandler}>&gt;</button>
                </div>
            </div>
        );
    }
}

const mapStateToProps = ({viewer}: ApplicationState, ownProps: ExperienceViewerProps) => {
    const {id} = ownProps.model;
    const experience = viewer.experiences[id];
    const {transitionDirection} = experience!;

    return {
        ...ownProps, transitionDirection
    } as ExperienceViewerProps;
};

const mapDispatchToProps = (
    dispatch: Dispatch<ApplicationState>,
    ownProps: ExperienceViewerProps
): ExperienceViewerProps => ({
    previousClickHandler() {
        dispatch(previousScene(ownProps.model.id));
    },
    nextClickHandler() {
        dispatch(nextScene(ownProps.model.id));
    },
    ...ownProps
});

export default connect(mapStateToProps, mapDispatchToProps)(ExperienceViewer);



