import {compose, withProps} from "recompose";
import {connect} from "react-redux";

import {ExperienceViewer, ExperienceViewerProps} from "../components/ExperienceViewer";
import {ApplicationState} from "../store";

import {Rect} from "shared/xp/geom";
import {ExperienceModel} from "shared/xp/models";

interface ExperienceProps {
    model: ExperienceModel;
    scene: number;
}

interface FrameProps {
    frame: Rect;
}

const mapViewerStateToProps = ({viewer}: ApplicationState) => {
    const {content} = viewer;

    if (content && content.experience) {
        return {
            model: content.experience.model,
            scene: content.experience.currentSceneNumber
        };
    }

    return {};
};

export default compose<ExperienceViewerProps, {}>(
    connect(mapViewerStateToProps),
    withProps<FrameProps, ExperienceProps>(({model}) => ({frame: new Rect(0, 0, model.width, model.height)}))
)(ExperienceViewer);



