import {compose, mapProps} from "recompose";
import withWindowLayout from "./withWindowLayout";
import {ExperienceModel} from "shared/xp/models";
import Size from "shared/xp/geom/Size";

interface PropsWithModel {
    model: ExperienceModel;
}

const experienceSizePropsMapper = (props: PropsWithModel) => {
    const {model} = props;
    return {
        ...props,
        originalSize: new Size(model.width, model.height)
    };
};

export default compose(mapProps(experienceSizePropsMapper), withWindowLayout);



