import * as React from "react";
import {Component} from "react";
import {Dispatch} from "redux";
import {connect} from "react-redux";
import {compose} from "recompose";
import {ApplicationState} from "../../store";
import {loadExperience} from "../../actions";

import {HOC} from "shared/xp/hoc";
import {ExperienceModel} from "shared/xp/models";

interface BaseComponentProps {
    model: ExperienceModel;
}

interface HOCProps {
    dispatch: Dispatch<ApplicationState>;
    experienceId: string;
    model: ExperienceModel;
}

/**
 * A HOC hooked up to the Redux store via `connect`. Given an `experienceId`, will dispatch
 * the `loadExperience` action to fetch and obtain the associated experience model. The
 * experience model will be passed to the `BaseComponent` via the `model` prop.
 */
const withExperienceModel: HOC<BaseComponentProps, HOCProps> = BaseComponent =>
    class extends Component<HOCProps> {
        static defaultProps = {
            model: null
        };

        componentWillReceiveProps({experienceId}: HOCProps) {
            if (experienceId && this.props.experienceId !== experienceId) {
                this.signalExperienceLoad(experienceId);
            }
        }

        componentDidMount() {
            this.signalExperienceLoad();
        }

        signalExperienceLoad(id?: string) {
            const {dispatch, experienceId} = this.props;

            dispatch(loadExperience(id || experienceId));
        }

        render() {
            const {model} = this.props;

            if (model) {
                return <BaseComponent {...this.props} />;
            }

            return (
                <span role="img" aria-label="Loading">
                    
                </span>
            );
        }
    };

const mapStateToProps = ({viewer}: ApplicationState, {experienceId}: HOCProps) => {
    const {experiences} = viewer;
    const state = experiences[experienceId];

    if (state) {
        return {model: state.model};
    }

    return {};
};

export default compose<HOCProps, BaseComponentProps>(
    connect(mapStateToProps),
    withExperienceModel
);



