import * as React from "react";
import {CSSProperties, StatelessComponent} from "react";
import {wrapDisplayName} from "recompose";

import {HOC} from "shared/xp/hoc";
import {StyledWidget} from "shared/xp/models";

const DEFAULT_BORDER_COLOR = "#000";
const DEFAULT_SCALE = 1;

export const withWidgetStyling: HOC<{}, StyledWidget> = BaseComponent => {
    const hoc: StatelessComponent<StyledWidget> = props => {
        const {radius, opacity, borderWidth, borderColor, boxShadow, ...childProps} = props;
        const style: CSSProperties = {
            overflow: "hidden",
            position: "relative",
            width: "100%",
            height: "100%",
            opacity,
            boxShadow
        };

        if (radius) {
            style.borderRadius = `${parseInt(radius, 10) * DEFAULT_SCALE}px`;
        }

        if (borderWidth && borderWidth > 0) {
            style.border = `${borderWidth}px solid ${borderColor || DEFAULT_BORDER_COLOR}`;
        }

        return (
            <div style={style}>
                <BaseComponent {...childProps} />
            </div>
        );
    };

    hoc.displayName = wrapDisplayName(BaseComponent, "withWidgetStyling");

    return hoc;
};



