import {Dispatch, MiddlewareAPI} from "redux";
import {ActionTypes, loadCompanySettings, Actions} from "../../actions";
import {ApplicationState}  from "../../store";

/**
 * A Redux middelware that will load the company settings after the initial experience has been loaded.
 */
export default (store: MiddlewareAPI<ApplicationState>) => (next:Dispatch<ApplicationState>) => (action: Actions) => {
    if (action.type === ActionTypes.EXPERIENCE_LOADED) {
        const {company} = store.getState();

        if (!company.model) {
            const {companyId} = action.payload.data!;
            store.dispatch(loadCompanySettings(companyId));
        }
    }

    next(action);
};



