import {request} from "../net";
import config from "shared/xp/config";
import {ExperienceModel, BreakpointGroupModel, CompanySettingsModel} from "shared/xp/models";

const VIEWER_API_BASE_URL = `${config.viewerApiBaseUrl}/api`;

const pathToSpecificResource = (collection: string, id: string) => (
    `${VIEWER_API_BASE_URL}/${collection}/${id}`
);

const pathToCollectionOnResource = (collection: string, id: string, subCollection: string) => (
    `${pathToSpecificResource(collection, id)}/${subCollection}`
);

export const fetchExperience = (id: string) => (
    request<ExperienceModel>(pathToSpecificResource("experiences", id))
);

export const fetchGroup = (id: string) => (
    request<BreakpointGroupModel>(pathToSpecificResource("groups", id))
);

export const fetchCompanySettings = (id: string) => (
    request<CompanySettingsModel>(pathToCollectionOnResource("companies", id, "settings"))
);



