import * as React from "react";
import * as ReactDOM from "react-dom";
import {Component, Children, ReactElement, cloneElement} from "react";
import {TweenLite} from "gsap";
import {SceneModel} from "shared/xp/models";
import {SceneProps} from "./Scene";

interface SceneFadeTransitionProps {
    /**
     * The current scene to show.
     */
    scene: SceneModel;
}

/**
 * Manages the fade transition between scenes. Layouts it's children on top
 * of each other, fading the current scene from `0` to `1` opacity.
 *
 * Takes the current `SceneModel` as it's only property.
 */
class SceneFadeTransition extends Component<SceneFadeTransitionProps> {
    shouldComponentUpdate({scene}: SceneFadeTransitionProps) {
        return scene !== this.props.scene;
    }

    render() {
        const {children, scene} = this.props;

        return Children.map(children, child => {
            const element = child as ReactElement<SceneProps>;
            const {model} = element.props;
            const props: Partial<SceneProps> = {
                active: model === scene,
                style: {
                    position: "absolute",
                    top: 0,
                    left: 0
                }
            };

            // Utilize the "ref" api in order to obtain the DOM node needed
            // for the fade transition - see `componentDidUpdate`.
            // We need to wrap the element/component inside a div, as stateless `function`
            // components cannot be given refs (the `Scene` component is stateless)
            return <div ref={model.id}>{cloneElement(element, props)}</div>;
        });
    }

    componentDidUpdate() {
        const {id} = this.props.scene;
        const node = ReactDOM.findDOMNode(this.refs[id]);

        TweenLite.fromTo(node, 1, {opacity: 0}, {opacity: 1.0});
    }
}

export default SceneFadeTransition;



