import * as React from "react";
import {Component, CSSProperties} from "react";
import "isomorphic-fetch";
import {DOMParser, XMLSerializer} from "xmldom";

export interface ExternalSvgIconComponentProps {
    iconSvgPath: string;
    iconStyle: CSSProperties;
    iconSize: number;
}

export interface ExternalSvgIconComponentState {
    isLoading: boolean;
    svgData: string ;
    error?: Error;
}

export class ExternalSvgIconComponent extends Component<ExternalSvgIconComponentProps, ExternalSvgIconComponentState> {

    constructor(props: ExternalSvgIconComponentProps) {
        super(props);
        this.state = {
            isLoading: true,
            svgData: ""
        };
    }

    async componentDidMount() {
        // fetch SVG data...
        const {iconSvgPath} = this.props;
 
        await fetch(iconSvgPath, {
            mode: "cors"
        }).then((response: Response) => {
            if(response.ok) {
                return response.text();    
            }
            throw new Error("Network response: ExternalSvgIcon");
         })
        .then((responseText: string) => {
            if(!responseText) {
                throw new Error("Response empty: ExternalSvgIcon");
            }
            // set the size of the SVG element within its XML...
            // NOTE: this not ideal - however if the SVG data is added inline,
            // React will display the XML string rather than rendering an SVG element.
            // 
            // potential alternative: a custom SVG-to-React component 
            // to translate SVG child nodes to React components.
            // these exist as node modules, and need to be ported to Typescript.
            let xmlDoc: XMLDocument = new DOMParser().parseFromString(responseText, "application/xml");
            let iconSizeString: string = String(this.props.iconSize);
            xmlDoc.getElementsByTagName("svg")[0].setAttribute("width", iconSizeString);
            xmlDoc.getElementsByTagName("svg")[0].setAttribute("height", iconSizeString);

            this.setState((current) => ({
                ...current,
                svgData: new XMLSerializer().serializeToString(xmlDoc),
                isLoading: false
            }));
        }).catch((error: Error) => {
            this.setState((current) => ({
                ...current,
                error: error,
                isLoading: false
            }));
        });

        return Promise.resolve();
    }

    render() {
        const {iconStyle} = this.props;
        const {error, isLoading, svgData} = this.state;

        if(error || isLoading) {
            return (
                <i/>
            );
        }
        return (
            <i style={iconStyle} dangerouslySetInnerHTML={{__html: svgData}} />
        );
    }

}


