import {Action} from "./actions";
import {
    IconModel,
    ButtonWidgetTypes,
    FontStyleType,
    FontWeightType
} from "./interfaces";

// TODO
interface Animation {}

// TODO
interface Effect {}

type ObjectId = string;

export const enum WidgetTypes {
    LINE = "line",
    SHAPE = "shape",
    IMAGE = "image",
    LINK = "link",
    SCENE_NAVIGATOR = "scene-navigator",
    EMBED = "embed",
    YOUTUBE = "youtube",
    SOCIAL_SHARING = "social-sharing",
    TEXT = "text",
    BUTTON = "button",
    COMPOSITE = "composite"
}

export interface BaseWidget {
    type: WidgetTypes;
    top: number;
    left: number;
    width: number;
    height: number;
    rotation: number;
    layerIndex: number;
    effects: ReadonlyArray<Effect>;
    animations: ReadonlyArray<Animation>;
    // This seems to be also of type `actions`, Might need to see if there's experience using this syntax or not.
    action?: Action;
}

export interface StyledWidget extends BaseWidget {
    radius?: string;  // number wrapped in a string
    borderWidth?: number;
    borderColor?: string;
    opacity?: number;
    boxShadow?: string;
}

interface LineWidget extends BaseWidget {
    type: WidgetTypes.LINE;
    lineColor: string;
    lineHeight: string;  // number wrapped in a string
}
export type LineWidgetModel = Readonly<LineWidget>;

interface ShapeWidget extends StyledWidget {
    type: WidgetTypes.SHAPE;
    backgroundColor: string;
}
export type ShapeWidgetModel = Readonly<ShapeWidget>;

interface ImageWidget extends StyledWidget {
    type: WidgetTypes.IMAGE;
    imageId: ObjectId;
    src?: string;
    srcSet?: string;
    sourceType?: "Scene7Integration";
    altText: string;
    originalImageWidth: number;
    originalImageHeight: number;
    borderRadius: number;
    supportsResize: boolean;
}
export type ImageWidgetModel = Readonly<ImageWidget>;

interface ButtonWidget extends StyledWidget {
    type: WidgetTypes.BUTTON;
    icon: ReadonlyArray<IconModel>;
    label: string;
    backgroundColor: string;
    borderColor: string;
    buttonType: ButtonWidgetTypes;
    cid: string;
    color: string;
    fontFamily: string;
    fontSize: number;
    fontStyle: FontStyleType;
    fontWeight: FontWeightType;
    layerIndex: number;
    opacity: number;
    padding: number;
}

export type ButtonWidgetModel = Readonly<ButtonWidget>;

interface LinkWidget extends BaseWidget {
    type: WidgetTypes.LINK;
}
export type LinkWidgetModel = Readonly<LinkWidget>;

interface SceneNavigatorWidget extends BaseWidget {
    type: WidgetTypes.SCENE_NAVIGATOR;
    direction: "horizontal" | "vertical";
    theme:
        | "white-1"
        | "white-2"
        | "white-3"
        | "white-4"
        | "white-5"
        | "black-1"
        | "black-2"
        | "black-3"
        | "black-4"
        | "black-5";
}
export type SceneNavigatorWidgetModel = Readonly<SceneNavigatorWidget>;

interface EmbedWidget extends BaseWidget {
    type: WidgetTypes.EMBED;
    url: string;
}
export type EmbedWidgetModel = Readonly<EmbedWidget>;

interface YoutubeWidget extends BaseWidget {
    type: WidgetTypes.YOUTUBE;
    videoID: string;
    autoplay: boolean;
    controls: boolean;
    loop: boolean;
    mute: boolean;
}
export type YoutubeWidgetModel = Readonly<YoutubeWidget>;

export enum SocialSharingWidgetStyleCategory {
    FlatSquared =  "flat-squared",
    FlatRounded =  "flat-rounded",
    Isolated    =  "isolated",
    FlatCircle  =  "flat-circle",
    WhiteCircle =  "white-circle",
    BlackCircle =  "black-circle"
}

export enum SocialSharingWidgetStyleName {
    Squared  = "Squared",
    Rounded  = "Rounded",
    Isolated = "Isolated",
    Circle   = "Circle",
    White    = "White",
    Black    = "Black"
}

export enum SocialSharingWidgetNetwork {
    Facebook   = "facebook",
    Twitter    = "twitter",
    Linkedin   = "linkedin",
    Pinterest  = "pinterest",
    GooglePlus = "googleplus"
}

interface SocialSharingWidget extends BaseWidget {
    type: WidgetTypes.SOCIAL_SHARING;
    style: SocialSharingWidgetStyleCategory;
    url?: string;
    networks: ReadonlyArray<SocialSharingWidgetNetwork>;
}
export type SocialSharingWidgetModel = Readonly<SocialSharingWidget>;

export interface CustomFontMetadata {
    customFontId: string;
    format: "truetype" | "woff" | "opentype";
    path: string;
}

export interface FontsDotComMetadata {
    projectId: string;
}

export interface TypekitMetadata {
    kitID: string;
}

export interface FontFamily {
    fontProvider: string;
    fontMetadata?: CustomFontMetadata | FontsDotComMetadata | TypekitMetadata;
    fontFamily: string;
    fontStyle: string;
    fontWeight: string;
}

interface TextWidget extends BaseWidget {
    type: WidgetTypes.TEXT;
    color: string;
    disableCentering: true; // always true
    fontFamilies: FontFamily[];
    cssFamilyNames: string[];
    fontProvider: string;
    fontFamily: string;
    fontSize: number;
    fontStyle: string;
    fontVariant: string;
    fontWeight: string;
    layerIndex: number;
    letterSpacing: number;
    lineHeight: number;
    text: string;
    textAlign: string;
    textDecoration: string;
    textShadow: string;
    textTransform: string;
}

export type TextWidgetModel = Readonly<TextWidget>;

interface CompositeWidget extends BaseWidget {
    type: WidgetTypes.COMPOSITE;
    group: ReadonlyArray<Widget>;
}

export type CompositeWidgetModel = Readonly<CompositeWidget>;

export type Widget = Readonly<
      LineWidget
    | ShapeWidget
    | ImageWidget
    | LinkWidget
    | SceneNavigatorWidget
    | EmbedWidget
    | YoutubeWidget
    | SocialSharingWidget
    | ButtonWidget
    | TextWidget
    | CompositeWidget
>;



