import * as React from "react";
import {StatelessComponent} from "react";
import {compose} from "recompose";
import {withDefaultWidgetBehavior} from "../hoc";

import {SocialSharingWidgetModel, SocialSharingWidgetStyleCategory, SocialSharingWidgetNetwork} from "../../../models";
import {openShareLink} from "./SocialSharingService";
import config from "../../../config";

import * as css from "./style.css";

interface SocialSharingWidgetComponent {
    style: SocialSharingWidgetStyleCategory;
    networks: ReadonlyArray<SocialSharingWidgetNetwork>;
    url?: string;
}

export const SocialSharingWidgetComponent: StatelessComponent<SocialSharingWidgetComponent> = (props) => {
    const {style, networks, url} = props;

    const socialSharingList = networks.map((network) => {
        const imagePath = `images/sharing/${style}/${network}.png`;
        const viewerBaseUrl = config.viewerBaseUrl ? config.viewerBaseUrl + "/" : "";
        const codePath = config.codePath ? config.codePath + "/" : "";
        const socialSharingImageSrc = `${viewerBaseUrl}${codePath}${imagePath}`;

        const clickHandler = () => openShareLink(network, url);
        return (
            <a data-network={network} className={css.shareLink} key={network} onClick={clickHandler}>
                <img src={socialSharingImageSrc}/>
            </a>
        );
    });

    return <div>{socialSharingList}</div>;
};

export default compose<SocialSharingWidgetComponent, SocialSharingWidgetModel>(
    withDefaultWidgetBehavior
)(SocialSharingWidgetComponent);



