import {GroupAction, ActionTypes} from "../actions";
import {GroupState} from "../store";

const DEFAULT_STATE: GroupState = {
    model: null
};

export default (state: GroupState = DEFAULT_STATE, action: GroupAction): GroupState => {
    switch (action.type) {
        case ActionTypes.GROUP_LOADED:
            return {...state, model: action.payload};
        default:
            return state;
    }
};



