import {Dispatch} from "redux";
import {ApplicationState} from "../../store";
import {fetchCompanySettings} from "../../api";
import {
    ActionTypes,
    CompanySettingsLoadAction,
    CompanySettingsLoadedAction,
    CompanySettingsLoadFailedAction
} from "../";

import {CompanySettingsModel} from "shared/xp/models";

const signalCompanySettingsLoad = (id: string): CompanySettingsLoadAction => ({
    type: ActionTypes.COMPANY_SETTINGS_LOAD,
    payload: id
});
const signalCompanySettingsLoaded = (settings: CompanySettingsModel): CompanySettingsLoadedAction => ({
    type: ActionTypes.COMPANY_SETTINGS_LOADED,
    payload: settings
});
const signalCompanySettingsLoadFailed = (error: Error): CompanySettingsLoadFailedAction => ({
    type: ActionTypes.COMPANY_SETTINGS_LOAD_FAILED,
    payload: error
});

export const loadCompanySettings = (id: string) => async (dispatch: Dispatch<ApplicationState>) => {
    dispatch(signalCompanySettingsLoad(id));

    try {
        const experienceJson = await fetchCompanySettings(id);
        dispatch(signalCompanySettingsLoaded(experienceJson));
    } catch (error) {
        dispatch(signalCompanySettingsLoadFailed(error));
    }
};



