import * as React from "react";
import {StatelessComponent, Component, CSSProperties} from "react";
import {compose} from "recompose";
import {withDefaultWidgetBehavior} from "../hoc";
import {YoutubeWidgetModel} from "../../../models";
import fetchYouTubeIframeAPI from "./fetchYouTubeIframeAPI";
import getPlayerVars from "./getPlayerVars";
import {ActivableWidgetComponentProps} from "../";

const IFRAME_PLAYER_STYLE: CSSProperties = {
    position: "absolute",
    top: 0,
    left: 0,
    width: "100%",
    height: "100%"
};

interface YouTubeWidgetComponentProps extends ActivableWidgetComponentProps {
    videoID?: string;
    loop?: boolean;
    controls?: boolean;
    autoplay?: boolean;
}

interface YouTubeWidgetComponentState {
    playerReady: boolean;
}

interface YouTubePosterImageProps {
    videoID: string;
}

export const YouTubePosterImage: StatelessComponent<YouTubePosterImageProps> = ({videoID}) => {
    const videoPreviewImageUrl = `//img.youtube.com/vi/${videoID}/hqdefault.jpg`;

    const style = {
        width: "100%",
        height: "100%",
        backgroundSize: "cover",
        backgroundImage: `url("${videoPreviewImageUrl}")`
    };

    return <div style={style} />;
};

export class YouTubeWidgetComponent extends Component<YouTubeWidgetComponentProps, YouTubeWidgetComponentState> {
    state = {
        playerReady: false
    };

    private player: YT.Player;

    private playerElement: HTMLElement | null;

    render() {
        const {videoID} = this.props;

        return videoID && (
            <div
                key={videoID}
                style={IFRAME_PLAYER_STYLE}
                ref={node => {
                    this.playerElement = node;
                }}
            >
                <YouTubePosterImage videoID={videoID} />
            </div>
        );
    }

    async componentDidMount() {
        const {videoID, controls, loop} = this.props;

        if (this.playerElement !== null && videoID) {
            const {Player} = await fetchYouTubeIframeAPI();
            const options: YT.PlayerOptions = {
                playerVars: getPlayerVars(videoID, loop, controls),
                videoId: videoID,
                events: {
                    onReady: () => this.setState({playerReady: true})
                }
            };

            this.player = new Player(this.playerElement, options);
        }
    }

    componentDidUpdate() {
        const {playerReady} = this.state;
        const {active, autoplay} = this.props;

        if (playerReady && autoplay) {
            if (active) {
                this.player.playVideo();
            } else {
                this.player.pauseVideo();
            }
        }
    }

    componentWillUnmount() {
        const {playerReady} = this.state;

        if (playerReady) {
            this.player.destroy();
        }
    }
}

export default compose<YouTubeWidgetComponentProps, YoutubeWidgetModel>(withDefaultWidgetBehavior)(
    YouTubeWidgetComponent
);



