import * as React from "react";
import {CSSProperties, StatelessComponent} from "react";
import {wrapDisplayName} from "recompose";

import {HOC} from "../../../hoc";
import {WidgetActionType, LinkAction} from "../../../models";
import {BaseWidgetComponentProps} from "../BaseWidgetComponentProps";
import {WidgetActionHandler} from "./withAction";

import {WithPixels} from ".";

// Question: Dispatching anything to the store would
// require us to get the experienceId down to the action level
// (aka.) SceneNavigation
export const withDefaultWidgetBehavior:
    HOC<{}, BaseWidgetComponentProps & WithPixels & WidgetActionHandler> = BaseComponent => {
    const hoc: StatelessComponent<BaseWidgetComponentProps & WithPixels& WidgetActionHandler> = props => {
        const {type, pxTop, pxLeft, pxWidth, pxHeight, rotation, widgetAction, ...childProps} = props;

        const className = `widget-component ${type}-widget`;
        const style: CSSProperties = {
            position: "absolute",
            top: `${pxTop}px`,
            left: `${pxLeft}px`
        };

        if (pxWidth) {
            style.width = `${pxWidth}px`;
        }

        if (pxHeight) {
            style.height = `${pxHeight}px`;
        }

        let onClickHandler = undefined;
        let encapsulateElement = undefined;

        if (rotation !== 0) {
            style.transform = `rotate(${rotation}deg)`;
            // todo add support for "stretch scaling". @see /src/shared/ui/experience/widgets/AbstractWidgetView.js#213
            // If we're "stretch scaling", compensate for the fact that scaling is done
            // from the center (transformOrigin 50% 50%) - and adjust the position.
            style.transformOrigin = "0 0 0";
        }

        if (props.action && widgetAction) {
            onClickHandler = () => widgetAction.execute();

            if (props.action.type === WidgetActionType.LinkAction) {
                encapsulateElement = () => {
                    const linkAction = props.action as LinkAction;

                    const linkStyle: CSSProperties = {
                        textDecoration: "none"
                    };

                    // set title for SEO on link widgets
                    let linkTitle: string | undefined = type === "link" ? linkAction.url : undefined;

                    return (
                        <a href={linkAction.url} target={linkAction.target} title={linkTitle} style={linkStyle} >
                            <BaseComponent {...childProps} />
                        </a>
                    );
                };
            }
        }

        return (
            <div style={style} className={className} onClick={onClickHandler}>
                {encapsulateElement ? encapsulateElement() : <BaseComponent {...childProps} />}
            </div>
        );
    };

    hoc.displayName = wrapDisplayName(BaseComponent, "withDefaultWidgetBehavior");

    return hoc;
};



