import * as React from "react";
import {PureComponent} from "react";
import {Route, Switch, Router} from "react-router";
import {History, createHashHistory} from "history";
import {connect, Dispatch} from "react-redux";
import ExperienceViewerRoute from "./ExperienceViewerRoute";
import BreakpointGroupViewerRoute from "./BreakpointGroupViewerRoute";
import {ApplicationState} from "../store";

interface RouterBasedViewerProps {
    dispatch: Dispatch<ApplicationState>;
    experienceId: string;
    sceneNumber: number;
}

class RouterBasedViewer extends PureComponent {

    private history: History;

    componentWillMount() {
        this.history = createHashHistory();
    }

    componentWillReceiveProps(nextProps: RouterBasedViewerProps) {
        const {experienceId, sceneNumber} = nextProps;
        // /:experienceId/:scene
        const path = `/${experienceId}/${sceneNumber}`;

        if (path !== this.history.location.pathname) {
            this.history.push(path);
        }
    }

    render() {
        return (
            <Router history={this.history}>
                <Switch>
                    <Route path="/group/:groupId" component={BreakpointGroupViewerRoute} />
                    <Route path="/:experienceId/:scene" component={ExperienceViewerRoute} />
                    <Route path="/:experienceId" component={ExperienceViewerRoute} />
                </Switch>
            </Router>
        );
    }
}

const mapStateToProps = ({viewer}: ApplicationState) => {
    if (viewer.content && viewer.content.experience) {
        const {experience} = viewer.content;

        if (experience.model) {
            return {
                experienceId: experience.model.id,
                sceneNumber: experience.currentSceneNumber
            };
        }
    }

    return {};
};

export default connect(mapStateToProps)(RouterBasedViewer);



