import {combineReducers} from "redux";

import viewer from "./viewerReducer";
import company from "./companyReducer";
import {ApplicationState} from "../store";

// The reason for the cast to `{}` is due to the typings for redux.
// The `Reducer` type requires the action type to be of type AnyAction,
// we are extending from the Action.
// Once the typings have been fixed with default generics, we should be able to remove the casting.
// The 'viewerReducer' and 'companyReducer' can't explicity be typed to Reducers because of the AnyActions.
// Ref: https://github.com/reactjs/redux/pull/2467 this commit breaks our flow.
// It has been fixed but not yet released see: https://github.com/reactjs/redux/blob/next/index.d.ts#L49
export default combineReducers<ApplicationState>({
    company,
    viewer
} as {});



