import * as React from "react";
import {Middleware} from "redux";
import {render} from "react-dom";
import {Provider} from "react-redux";
import {DEFAULT_STATE, createStore,ViewerMode} from "./store";
import ViewerXPApplication from "./app/ViewerXPApplication";
import {initializeViewer} from "./actions";
import {companySettingsMiddleware} from "./store/middleware";
import {initializeEnvironment} from "./viewer-xp-browser";

const store = createStore(DEFAULT_STATE, companySettingsMiddleware as Middleware);
const {dispatch} = store;

initializeEnvironment();

dispatch(initializeViewer(ViewerMode.STANDALONE));

render(
    <Provider store={store}>
        <ViewerXPApplication />
    </Provider>,
    document.getElementById("viewer")
);



