/**
 * Convenience for getting the YouTube player vars.
 *
 * @param videoID The video id.
 * @param loop Whether to loop the video.
 * @param controls Whether to show controls.
 */
export default (videoID: string, loop: boolean = false, controls: boolean = true) => {
    const playerVars: YT.PlayerVars = {
        color: "white",
        modestbranding: 0,
        rel: 0,
        showinfo: 0,
        controls: +controls,
        loop: +loop
    };

    if (playerVars.loop) {
        playerVars.playlist = videoID;
    }

    return playerVars;
};



