import * as React from "react";
import {StatelessComponent} from "react";
import {compose} from "recompose";

import {withDefaultWidgetBehavior} from "./hoc";
import {LineWidgetModel} from "../../models";

interface LineWidgetComponentProps {
    lineColor: string;
    lineHeight: string;
}

export const LineWidgetComponent: StatelessComponent<LineWidgetComponentProps> = (props) => {
    const {lineColor, lineHeight} = props;
    const style = {
        backgroundColor: lineColor,
        height: lineHeight + "px",
        marginTop: -lineHeight / 2
    };

    return <div style={style} />;
};

export default compose<LineWidgetComponentProps, LineWidgetModel>(
    withDefaultWidgetBehavior
)(LineWidgetComponent);



