import {Size} from "shared/xp/geom";
import {debounce} from "underscore";

interface ViewportListenerCallback {
    (size: Size): void;
}

/**
 * Listens for `resize` and `orientationchange` events on the specified target (`Window`).
 *
 * @export
 * @class ViewportListener
 */
export default class ViewportListener {

    private viewportSizeChangedHandler: () => void;

    /**
     * Constructor.
     *
     * @param {ViewportListenerCallback} callback
     * @param {number} [debounceWait=300]
     * @param {Window} [target=window]
     */
    constructor(
        readonly callback: ViewportListenerCallback,
        readonly debounceWait: number = 300,
        readonly target: Window = window
    ) {
        this.viewportSizeChangedHandler = debounce(() => this.invokeCallback(), debounceWait);
    }

    listen() {
        this.target.addEventListener("resize", this.viewportSizeChangedHandler);
        this.target.addEventListener("orientationchange", this.viewportSizeChangedHandler);

        return this;
    }

    stopListening() {
        this.target.removeEventListener("resize", this.viewportSizeChangedHandler);
        this.target.removeEventListener("orientationchange", this.viewportSizeChangedHandler);

        return this;
    }

    /**
     * Immediately invokes the specified callback. Useful if you need the initial size.
     *
     * @returns {ViewportListener} A reference to `this` instance, for chaining convenience.
     */
    trigger() {
        this.invokeCallback();

        return this;
    }

    private invokeCallback() {
        const {innerWidth, innerHeight} = this.target;
        this.callback(new Size(innerWidth, innerHeight));
    }

}



