import * as React from "react";
import {HOC} from "../../../hoc";

type KeyOf<T> = keyof T;

/**
 * High Order Component which exctracts some property from the component's props and passed them to
 * base Component
 *
 * @param propertyNames - The property to pick from the base component props
 */
export function pickProps<TInner, TOuter extends Partial<TInner> = Partial<TInner>>(
    ...propertyNames: KeyOf<TInner>[]
): HOC<TOuter, TInner> {
    return (BaseComponent) => (props) => {
        let reducedProps = {} as TOuter;

        propertyNames.forEach(key => {
            reducedProps[key] = props[key];
        });
        return <BaseComponent {...reducedProps} />;
    };
}



