import {Dispatch} from "redux";
import {ApplicationState} from "../../store";
import {fetchGroup} from "../../api";
import {ActionTypes, GroupLoadAction, GroupLoadedAction, GroupLoadFailedAction} from "../";

import {BreakpointGroupModel} from "shared/xp/models";

const signalGroupLoad = (id: string): GroupLoadAction => ({type: ActionTypes.GROUP_LOAD, payload: id});
const signalGroupLoaded = (model: BreakpointGroupModel): GroupLoadedAction => ({
    type: ActionTypes.GROUP_LOADED,
    payload: model
});
const signalGroupLoadFailed = (error: Error): GroupLoadFailedAction => ({
    type: ActionTypes.GROUP_LOAD_FAILED,
    payload: error
});

export const loadGroup = (id: string) => async (dispatch: Dispatch<ApplicationState>) => {
    dispatch(signalGroupLoad(id));

    try {
        const model = await fetchGroup(id);
        dispatch(signalGroupLoaded(model));
    } catch (error) {
        dispatch(signalGroupLoadFailed(error));
    }
};



