import {ViewerApplicationState, ViewerMode} from "../store";
import contentReducer from "./contentReducer";
import {Actions, ActionTypes, ExperienceAction} from "../actions";
import experienceReducer from "./experienceReducer";

const DEFAULT_STATE: ViewerApplicationState = {
    mode: ViewerMode.STANDALONE,
    experiences: {}
};

const updateExperiences = ({experiences}: ViewerApplicationState, action: ExperienceAction) => {
    const {experienceId} = action.payload;
    const currentExperienceState = experiences[experienceId];
    const experienceState = experienceReducer(currentExperienceState, action);

    experiences[experienceId] = experienceState;

    // Should this be a new Map instance?
    return experiences;
};

export default (state = DEFAULT_STATE, action: Actions) => {
    switch (action.type) {
        case ActionTypes.VIEWER_INIT:
            return {
                ...state,
                mode: action.payload
            };
        case ActionTypes.EXPERIENCE_LOADED:
        case ActionTypes.EXPERIENCE_SCENE_CHANGED:
            return {
                ...state,
                experiences: updateExperiences(state, action),
                content: contentReducer(state.content, action)
            };
        case ActionTypes.EXPERIENCE_LOAD_FAILED:
            return {
                ...state,
                error: action.payload.data
            };
        case ActionTypes.GROUP_LOADED:
            return {
                ...state,
                content: contentReducer(state.content, action)
            };
        default:
            return state;
    }
};



