import {ViewerMode} from "./";
import {ExperienceModel, BreakpointGroupModel, CompanySettingsModel} from "shared/xp/models";
import TransitionDirection from "shared/xp/components/scenes/TransitionDirection";

export interface Experiences {
    [s: string]: ExperienceState;
}

export interface ViewerApplicationState {
    mode: ViewerMode;
    content?: ContentState;
    experiences: Experiences;
    error?: Error;
}

export interface ExperienceState {
    model?: ExperienceModel;
    currentSceneNumber: number;
    transitionDirection: TransitionDirection;
}

export interface GroupState {
    model: BreakpointGroupModel |null;
}

export interface ApplicationState {
    viewer: ViewerApplicationState;
    company: CompanyState;
}

export interface ContentState {
    experience?: ExperienceState;
    group?: GroupState;
}

export interface CompanyState {
    model?: CompanySettingsModel;
}

/**
 * Copy/paste from viewerReducer defaultState
 * Does the default state should live next to there types?
 */
const DEFAULT_VIEWER_STATE: ViewerApplicationState = {
    mode: ViewerMode.STANDALONE,
    experiences: {}
};

export const DEFAULT_STATE: ApplicationState = {
    viewer: DEFAULT_VIEWER_STATE,
    company: {}
};



