import {ApplicationConfiguration} from "./interfaces";

const {env: {
    APP_CONFIG,
    NODE_ENV
}} = process;

const providedConfig = typeof APP_CONFIG === "string" ? JSON.parse(APP_CONFIG) : APP_CONFIG;

export {ApplicationConfiguration};

export default {
    ...providedConfig,
    production: NODE_ENV === "production"
} as ApplicationConfiguration;



