import * as React from "react";

import {Size} from "../../../geom";
import {ImageWidgetModel, Integrations, IntegrationTypes, Scene7IntegrationModel} from "../../../models";
import {SceneNavigationComponent, Scene7ImageWidgetComponent, ImageWidgetComponent} from "./";

export default (widget: ImageWidgetModel, size: Size, integrations?: ReadonlyArray<Integrations>) => {
    if (widget.action && widget.action.type === "sceneNavigation") {
        return <SceneNavigationComponent {...widget} />;
    }
    const boxSize = new Size(size.width * (widget.width / 100), size.height * (widget.height / 100));

    if (widget.sourceType && widget.sourceType === "Scene7Integration") {
        let servingUrl = "";

        if (integrations) {
            const scene7Integration = integrations.find(i => i.type === IntegrationTypes.SCENE7) as
                | Scene7IntegrationModel
                | undefined;

            if (scene7Integration && scene7Integration.imageServingUrl) {
                servingUrl = scene7Integration.imageServingUrl;
            }
        }

        return <Scene7ImageWidgetComponent {...widget} baseUrl={servingUrl} frame={boxSize} />;
    }

    return <ImageWidgetComponent {...widget} frame={boxSize} />;
};



