import {Dispatch} from "redux";
import {ApplicationState, ViewerMode} from "../../store";
import {ActionTypes, ViewerInitAction, ViewerInitializedAction} from "../";

// Viewer
const signalViewerInit = (mode: ViewerMode): ViewerInitAction => ({type: ActionTypes.VIEWER_INIT, payload: mode});
const signalViewerInitialized = (): ViewerInitializedAction => ({type: ActionTypes.VIEWER_INITIALIZED, payload: {}});

export const initializeViewer = (mode: ViewerMode) => (dispatch: Dispatch<ApplicationState>) => {
    dispatch(signalViewerInit(mode));
    dispatch(signalViewerInitialized());
};



