import * as React from "react";
import {StatelessComponent, CSSProperties} from "react";
import {compose} from "recompose";
import {withDefaultWidgetBehavior} from "../hoc";
import {withIframeUrl} from "./hoc";
import {EmbedWidgetModel} from "../../../models";

interface EmbedWidgetComponentProps {
    url: string;
}

export const EmbedWidgetComponent: StatelessComponent<EmbedWidgetComponentProps> = (props) => {
    const {url} = props;

    const style: CSSProperties = {
        width:     "1px",
        minWidth:  "100%",
        height:    "1px",
        minHeight: "100%",

        // TODO: The below belongs to .widget-component iframe, should be moved to css?
        border: 0,
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        position: "absolute"
    };

    return (
            <iframe allowFullScreen={true} scrolling="auto" src={url} style={style}/>
    );
};

export default compose<EmbedWidgetComponentProps, EmbedWidgetModel>(
    withIframeUrl,
    withDefaultWidgetBehavior
)(EmbedWidgetComponent);



