import * as React from "react";
import {StatelessComponent} from "react";

export interface ButtonTextComponentProps {
    label: string;
}

export const ButtonTextComponent: StatelessComponent<ButtonTextComponentProps> = (props) => {

    const {label} = props;

    return (
        <span>{label}</span>
    );
};


