import {CustomFontMetadata, FontFamily, FontsDotComMetadata, TypekitMetadata} from "../models";
import {FontLoaderProvider} from "./FontLoader";
import config from "../config";
import * as WebFont from "webfontloader";

export class CustomFontLoaderProvider implements FontLoaderProvider {
    private cache: Set<String> = new Set();

    support(font: FontFamily): boolean {
        return typeof document !== "undefined" && font.fontProvider === "custom";
    }

    load(font: FontFamily): Promise<FontFamily | Error> {
        if (this.cache.has(font.fontFamily)) {
            return Promise.resolve(font);
        }

        const metadata = font.fontMetadata as CustomFontMetadata;
        const styleSheet = document.createElement("style");
        styleSheet.setAttribute("type", "text/css");
        styleSheet.innerHTML = `
            @font-face {
                font-family: "${font.fontFamily}";
                src: url(${config.viewerApiBaseUrl}/${metadata.path}) format("${metadata.format}")
            }
        `;
        document.head.appendChild(styleSheet);
        this.cache.add(font.fontFamily);
        return Promise.resolve(font);
    }

}

export class GoogleFontLoaderProvider implements FontLoaderProvider {
    support(font: FontFamily): boolean {
        return font.fontProvider === "google";
    }

    load(font: FontFamily): Promise<FontFamily | Error> {
        return new Promise((resolve, reject) => {
            WebFont.load({
                google: {
                    families: [`${font.fontFamily}:${font.fontWeight}`]
                },
                fontactive: () => resolve(font),
                fontinactive: () => reject(font)
            });
        });
    }

}

export class StandardFontLoaderProvider implements FontLoaderProvider {
    support(font: FontFamily): boolean {
        return font.fontProvider === "standard";
    }

    load(font: FontFamily): Promise<FontFamily | Error> {
        return Promise.resolve(font);
    }

}

export class TypekitFontLoaderProvider implements FontLoaderProvider {
    support(font: FontFamily): boolean {
        return font.fontProvider === "typekit";
    }

    load(font: FontFamily): Promise<FontFamily | Error> {
        return new Promise((resolve, reject) => {
            const metadata = font.fontMetadata as TypekitMetadata;
            WebFont.load({
                typekit: {
                    id: [metadata.kitID]
                },
                fontactive: () => resolve(font),
                fontinactive: () => reject(font)
            });
        });
    }

}

export class FontsDotComLoaderProvider implements FontLoaderProvider {
    /**
     * todo: not sure is it necessary
     * @type {number}
     * @private
     */
    static _version = Date.now();

    support(font: FontFamily): boolean {
        return font.fontProvider === "fontsDotCom";
    }

    load(font: FontFamily): Promise<FontFamily | Error> {
        return new Promise((resolve, reject) => {
            const metadata = font.fontMetadata as FontsDotComMetadata;
            WebFont.load({
                monotype: {
                    projectId: metadata.projectId,
                    version: FontsDotComLoaderProvider._version, // flushes the CDN cache
                },
                fontactive: () => resolve(font),
                fontinactive: () => reject(font)
            });
        });
    }

}



