import {Widget} from "../../../models";
import {Size} from "../../../geom";

export interface WithPixels {
    pxTop: number;
    pxHeight: number;
    pxLeft: number;
    pxWidth: number;
}

export const withPixelSize = (widget: Widget, size: Size): Widget & WithPixels => ({
    ...widget,
    pxTop: Math.round(widget.top * size.height / 100),
    pxLeft: Math.round(widget.left * size.width / 100),
    pxWidth: Math.round(widget.width * size.width / 100),
    pxHeight: Math.round(widget.height * size.height / 100)
});



