import {mapProps, compose} from "recompose";
import {RouteComponentProps} from "react-router";
import BreakpointGroupViewer from "./BreakpointGroupViewer";
import withBreakpointGroupModel from "../hoc/group/withBreakpointGroupModel";

interface GroupRouteParams {
    groupId: string;
}

const routePropsMapper = ({match}: RouteComponentProps<GroupRouteParams>) => ({groupId: match.params.groupId});

export default compose(
    mapProps(routePropsMapper),
    withBreakpointGroupModel
)(BreakpointGroupViewer);



