import * as React from "react";
import {StatelessComponent} from "react";
import {wrapDisplayName} from "recompose";

import {HOC} from "shared/xp/hoc";
import fontLoader from "../../../utils/FontLoader";
import {FontFamily} from "../../../models";

export const withWidgetFontLoading: HOC<{}, {fontFamilies:FontFamily[]}> = BaseComponent => {
    const hoc: StatelessComponent<{fontFamilies:FontFamily[]}> = props => {
        fontLoader.loadFromArray(props.fontFamilies);
        return (
            <BaseComponent {...props} />
        );
    };

    hoc.displayName = wrapDisplayName(BaseComponent, "withWidgetFontLoading");

    return hoc;
};



