import * as React from "react";
import {ReactElement, StatelessComponent} from "react";
import {SceneModel, SceneTransition, Integrations} from "shared/xp/models";
import Scene, {SceneProps} from "./Scene";
import SceneSlideTransition from "./SceneSlideTransition";
import SceneFadeTransition from "./SceneFadeTransition";
import {Size} from "../../geom";
import TransitionDirection from "./TransitionDirection";

const createCurrentSceneElements = (
    scenes: ReadonlyArray<SceneModel>,
    index: number,
    size: Size,
    integrations?: ReadonlyArray<Integrations>
): ReadonlyArray<ReactElement<SceneProps>> => {
    // TODO: We need preconditions.
    if (scenes.length === 1) {
        throw new Error("'computeCurrentScenes' needs at least 2 scenes.");
    }
    if (index >= scenes.length) {
        throw new RangeError(`Invalid index: ${index}. We only have ${scenes.length} scenes.`);
    }

    const maxIndex = scenes.length - 1;
    const nextIndex = index + 1 > maxIndex ? 0 : index + 1;
    const previousIndex = index - 1 < 0 ? maxIndex : index - 1;
    const currentScene = scenes[index];
    const nextScene = scenes[nextIndex];
    const previousScene = scenes[previousIndex];
    const props = {
        integrations,
        size
    };

    const sceneElements = [
        <Scene model={previousScene} hidden={true} key={previousScene.id} {...props} />,
        <Scene model={currentScene} key={currentScene.id} {...props} />
    ];

    if (scenes.length > 2) {
        sceneElements.push(<Scene model={nextScene} hidden={true} key={nextScene.id} {...props} />);
    }

    return sceneElements;
};

interface CurrentScenesProps {
    scenes: ReadonlyArray<SceneModel>;
    current: number;
    size: Size;
    transitionDirection: TransitionDirection;
    transition: SceneTransition;
    integrations?: ReadonlyArray<Integrations>;
}

const CurrentScenes: StatelessComponent<CurrentScenesProps> = ({
    scenes,
    current,
    integrations,
    size,
    transition,
    transitionDirection
}) => {
    const index = current - 1;
    const scene = scenes[index];
    const currentScenes = createCurrentSceneElements(scenes, index, size, integrations);

    return transition === SceneTransition.FADE ? (
        <SceneFadeTransition scene={scene}>{currentScenes}</SceneFadeTransition>
    ) : (
        <SceneSlideTransition
            scenes={scenes}
            scene={scene}
            transitionDirection={transitionDirection}
            sceneSize={size}
            transition={transition}
        >
            {currentScenes}
        </SceneSlideTransition>
    );
};

const StaticScene: StatelessComponent<SceneProps> = props => <Scene style={{position: "absolute"}} {...props} />;

interface ScenesProps {
    scenes: ReadonlyArray<SceneModel>;
    overlayScene: SceneModel;
    backgroundScene: SceneModel;
    current: number;
    integrations?: ReadonlyArray<Integrations>;
    size: Size;
    transition: SceneTransition;
    transitionDirection: TransitionDirection;
}

const Scenes: StatelessComponent<ScenesProps> = ({
    scenes,
    overlayScene,
    backgroundScene,
    integrations,
    size,
    transition,
    transitionDirection,
    current = 1
}) => {
    const singleOrMultipleScenes =
        scenes.length === 1 ? (
            <Scene model={scenes[0]} integrations={integrations} active={true} size={size} />
        ) : (
            <CurrentScenes
                current={current}
                scenes={scenes}
                integrations={integrations}
                size={size}
                transitionDirection={transitionDirection}
                transition={transition}
            />
        );

    return (
        <div>
            <StaticScene model={backgroundScene} integrations={integrations} size={size} active={true} />
            {singleOrMultipleScenes}
            <StaticScene model={overlayScene} integrations={integrations} size={size} active={true} />
        </div>
    );
};

export default Scenes;



