import {CompanySettingsAction, ActionTypes} from "../actions";
import {CompanyState} from "../store";

const DEFAULT_STATE = {

};

export default (state: CompanyState = DEFAULT_STATE, action: CompanySettingsAction): CompanyState => {
    if (action.type === ActionTypes.COMPANY_SETTINGS_LOADED) {
        return {model: action.payload};
    }

    return state;
};



