import {ExperienceAction, GroupAction, ActionTypes} from "../actions";
import experienceReducer from "./experienceReducer";
import groupReducer from "./groupReducer";
import {ContentState} from "../store";

export function contentReducer(
    state: ContentState = {},
    action: ExperienceAction | GroupAction): ContentState {
    switch (action.type) {
        case ActionTypes.EXPERIENCE_LOADED:
        case ActionTypes.EXPERIENCE_SCENE_CHANGED:
            return {
                ...state,
                experience: experienceReducer(state.experience, action)
            };
        case ActionTypes.GROUP_LOADED:
            return {
                ...state,
                group: groupReducer(state.group, action)
            };
        default:
            return state;
    }
}

export default contentReducer;



