import {Component, Children, cloneElement} from "react";
import {find} from "underscore";

import ViewportListener from "../util/ViewportListener";

import {Size} from "shared/xp/geom";
import {BreakpointGroupModel} from "shared/xp/models";

interface BreakpointObserverProps {
    model: BreakpointGroupModel;
}

interface BreakpointObserverState {
    experienceId: string;
}

export default class BreakpointObserver extends Component<BreakpointObserverProps, BreakpointObserverState> {

    private viewportListener: ViewportListener;

    componentWillMount() {
        this.viewportListener = new ViewportListener(this.updateStateFromCurrentSize)
            .listen()
            .trigger();
    }

    componentWillUnmount() {
        this.viewportListener.stopListening();
    }

    render() {
        const {children} = this.props;
        const child = Children.only(children);

        return cloneElement(child, {
            experienceId: this.state.experienceId
        });
    }

    private updateStateFromCurrentSize = ({width}: Size) => {
        const {entries} = this.props.model;
        const sortedEntries = entries.slice(0);

        sortedEntries.sort((a, b) => a.value.threshold - b.value.threshold);

        const entry = find(sortedEntries, (e, i) => {
            const next = sortedEntries[i + 1];
            const from = e.value.threshold;
            const to = (next && next.value.threshold) || Number.POSITIVE_INFINITY;

            return width >= from && width < to;
        });

        this.setState({experienceId: entry!.reference.resourceId});
    }
}



