import * as React from "react";

import {StatelessComponent} from "react";
import {compose} from "recompose";
import {
    withAction,
    withDefaultWidgetBehavior,
} from "./hoc";
import { TextWidgetModel } from "../../models";
import {DEFAULT_ACTIONS} from "./actions";
import {withWidgetFontLoading} from "./hoc/withWidgetFontLoading";

export interface TextWidgetComponentProps extends TextWidgetModel {}

export const TextWidgetComponent: StatelessComponent<TextWidgetComponentProps> = (props) => {
    const {
        color,
        fontSize,
        fontVariant,
        fontFamily,
        letterSpacing,
        lineHeight,
        textAlign,
        textDecoration,
        textShadow,
        textTransform
    } = props;

    const style = {
        color,
        fontSize,
        fontFamily,
        fontVariant,
        letterSpacing,
        lineHeight,
        textAlign,
        textDecoration,
        textShadow,
        textTransform
    };

    return <div style={style} dangerouslySetInnerHTML={{__html: props.text}} />;
};

export default compose<TextWidgetComponentProps, TextWidgetModel>(
    withAction(...DEFAULT_ACTIONS), withDefaultWidgetBehavior, withWidgetFontLoading
)(TextWidgetComponent);



