import * as React from "react";
import {StatelessComponent} from "react";
import {wrapDisplayName} from "recompose";
import {ActivableWidgetComponentProps} from "../../../../components/widgets";
import {HOC} from "../../../../hoc";

const VALID_REGEX = /^https:\/\/|^http:\/\/|^\//;
export const BLANK_URL = "about:blank";

export interface WithIframeUrlProps extends ActivableWidgetComponentProps {
    url: string;
}

export const withIframeUrl: HOC<WithIframeUrlProps, WithIframeUrlProps> = (BaseComponent) => {

    const hoc: StatelessComponent<WithIframeUrlProps> = (props) => {
        const {active, url} = props;

        const isValidUrl = VALID_REGEX.test(url);

        const iframeUrl = (!isValidUrl || !active) ? BLANK_URL : url;

        return <BaseComponent {...props} url={iframeUrl} />;
    };

    hoc.displayName = wrapDisplayName(BaseComponent, "withIframeUrl");

    return hoc;
};

export default withIframeUrl;



