import * as React from "react";
import {StatelessComponent} from "react";
import {connect} from "react-redux";

import RouterBasedViewer from "../containers/RouterBasedViewer";
import ChannelBasedViewer from "../containers/ChannelBasedViewer";
import {ApplicationState, ViewerMode} from "../store";

interface ViewerXPApplicationProps {
    mode: ViewerMode;
}

const ViewerXPApplication: StatelessComponent<ViewerXPApplicationProps> = ({mode}) =>
    mode === ViewerMode.STANDALONE ? <RouterBasedViewer /> : <ChannelBasedViewer />;

const mapStateToProps = ({viewer: {mode}}: ApplicationState) => ({mode});

export default connect(mapStateToProps)(ViewerXPApplication);



