import * as React from "react";

import {HOC} from "../../../../hoc";
import {ImageWidgetModel} from "../../../../models";
import {DomainUrl} from "./withBaseDomainUrl";
import {WidgetSize} from "../types";

export interface UrlScene7Props {
    baseUrl: string;
}

export type WithSizeAndUrlProps = ImageWidgetModel & WidgetSize & UrlScene7Props;
type OutputProps = ImageWidgetModel & DomainUrl;

export const withScene7SizeAndUrl: HOC<
    OutputProps,
    WithSizeAndUrlProps
> = BaseComponent => ({src, frame, baseUrl, ...props}) => {
    const url = `${baseUrl}/${src || ""}?size=${frame.width},${frame.height}`;

    return <BaseComponent {...props} url={url} srcUrls={url} />;
};



