import {ApplicationState} from "../store";
import {connect} from "react-redux";
import {mapProps, compose, lifecycle} from "recompose";
import {RouteComponentProps} from "react-router";
import ExperienceViewer, {ExperienceViewerProps} from "../components/ExperienceViewer";
import withExperienceModel from "../hoc/experience/withExperienceModel";
import withExperienceWindowLayout from "../hoc/viewer/withExperienceWindowLayout";
import {goToScene} from "../actions";

interface ExperienceRouteProps {
    experienceId: string;
    scene?: string;
}

interface MappedExperienceRouteProps {
    experienceId: string;
    sceneNumber: number;
    currentSceneNumber?: number;
    onSceneUpdate: (sceneNumber: number, experienceId: string) => void;
}

/**
 * Maps the needed properties (`experienceId` and `scene`) passed from the router.
 */
const routePropsMapper = ({match}: RouteComponentProps<ExperienceRouteProps>) => {
    const {experienceId, scene = "1"} = match.params;

    return {
        experienceId,
        sceneNumber: parseInt(scene, 10)
    };
};

const updateToScene = ({experienceId, sceneNumber, currentSceneNumber, onSceneUpdate}: MappedExperienceRouteProps) => {
    // Guard to only update state when change is triggered from the router layer, i.e. url change.
    if (!currentSceneNumber || currentSceneNumber !== sceneNumber) {
        onSceneUpdate(sceneNumber, experienceId);
    }
};

const updateStoreWithCurrentScene = compose(
    connect(
        ({viewer}: ApplicationState) => {
            if (viewer.content && viewer.content.experience) {
                return {
                    currentSceneNumber: viewer.content.experience.currentSceneNumber
                };
            }

            return {};
        },
        (dispatch) => ({
            onSceneUpdate: (sceneNumber: number, experienceId: string) => dispatch(goToScene(sceneNumber, experienceId))
        })
    ),
    lifecycle<MappedExperienceRouteProps, {}>({
        componentWillMount() {
            // Set the initial scene provided by the router.
            updateToScene(this.props);
        },
        componentWillReceiveProps(nextProps: MappedExperienceRouteProps) {
            updateToScene(nextProps);
        }
    })
);

/**
 * A enhanced version of the `ExperienceViewer` to be used as a route component.
 */
export default compose<ExperienceViewerProps, {}>(
    mapProps(routePropsMapper),
    updateStoreWithCurrentScene,
    withExperienceModel,
    withExperienceWindowLayout
)(ExperienceViewer);



