import * as React from "react";
import {StatelessComponent, CSSProperties} from "react";
import {compose} from "recompose";

import {withImageErrorHandler, LoadErrorHandler} from "./hoc";

const DEFAULT_IMG_STYLE: CSSProperties = {
    position: "absolute",
    width: "100%",
    height: "100%"
};

export interface ImageProps {
    url: string;
    srcUrls: string;
    altText: string;
}

export type ImageComponentProps = ImageProps & LoadErrorHandler;

export const Image: StatelessComponent<ImageComponentProps> = ({url, srcUrls, altText, onErrorHandler}) => {
    return <img src={url} srcSet={srcUrls} alt={altText} onError={onErrorHandler} style={DEFAULT_IMG_STYLE} />;
};

export default compose<ImageComponentProps, ImageProps>(
    withImageErrorHandler
)(Image);



