import * as React from "react";
import {StatelessComponent} from "react";
import {wrapDisplayName} from "recompose";

import {HOC} from "../../../../hoc";
import config from "../../../../config";

interface HOCProps {
    src: string;
    srcSet: string;
}

export interface DomainUrl {
    url: string;
    srcUrls: string;
}

export const withBaseDomainUrl: HOC<DomainUrl, HOCProps> = BaseComponent => {
    const hoc: StatelessComponent<HOCProps> = ({src, srcSet, ...props}) => {
        const url = `${config.viewerApiBaseUrl}/${src}`;

        // append viewerApiBaseUrl to begining of each file string in srcset urls
        const srcUrls = srcSet.replace(/^/gm, `${config.viewerApiBaseUrl}/`);

        return <BaseComponent {...props} url={url} srcUrls={srcUrls} />;
    };

    hoc.displayName = wrapDisplayName(BaseComponent, "withBaseDomainUrl");

    return hoc;
};

export default withBaseDomainUrl;



